html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: var(--main-text-color-black);
  background-color: var(--primary-bg-color);

  background-repeat: no-repeat;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

h1,
h2,
h3,
p,
ul {
  margin: 0px;
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}
